@import '~@wp-plugins/adapters/src/theme/variables';

@mixin mediaField($tokenLength, $usrLength, $updateLength) {
	.media-type {
		flex: 0 0 $tokenLength;
	}
	.source {
		flex: 0 0 $usrLength;
	}
	.caption {
		flex: 0 0 $updateLength;
	}
}

.single-media {
	&__fields {
		justify-content: space-between;
		flex-flow: row wrap;
		.form-control {
			width: auto;
			padding-inline-end: 1.5em;
			padding-inline-start: 1.5em;
			&__label {
				font-weight: 400;
			}
		}
		@include mediaField(30%, 35%, 35%);
		@media screen and (max-width: $breakpointLg) {
			@include mediaField(100%, 100%, 100%);
		}
		@media screen and (min-width: $breakpointLg) {
			@include mediaField(30%, 35%, 35%);

			.form-control {
				flex-direction: column;
			}
		}
	}
}
