@import '~@wp-plugins/adapters/src/theme/variables';

.post-type-wptgpro_p2tg {
	// Hide admin notices
	#wpbody-content div.wrap {
		/* Fix WP Style conflicts */
		@media screen and (max-width: $breakpointLg) {
			padding-inline-start: 10px;
		}
		#titlediv {
			#title-prompt-text {
				padding: 0px !important;
				padding-inline-start: 10px !important;
				top: 3px;
			}
			#title {
				border: 1px solid #e2e8f0;
			}
		}
		& > div.error {
			display: none;
		}
	}
	#wptelegram_pro_p2tg_cpt_mb {
		border-width: 0px !important;
		background: transparent;
		.misc-keyboard-block {
			display: flex;
			flex-direction: column;
			@media screen and (min-width: $breakpointXXl) {
				flex-direction: row;
				.miscellaneous-settings,
				.message-keyboard {
					flex-basis: 50%;
					&.message-keyboard {
						margin-left: 1em;
					}
				}
			}
		}
	}

	// hide pos submit box
	div#postbox-container-1 {
		display: none;
	}
	div#postbox-container-2 {
		div#normal-sortables {
			.postbox:not(#wptelegram_pro_p2tg_cpt_mb) {
				display: none;
			}
		}
		div#advanced-sortables {
			display: none;
		}
	}
}
